.title-container {
    border: 1px solid var(--bright-border-color);
    padding: 1rem 2rem;
    border-radius: 5px;
    margin-bottom: 2rem;
    /* REMOVED width: 100%; to allow the box to shrink-to-fit */
}

.text-container {
    border: 1px solid var(--border-color);
    padding: 2rem;
    border-radius: 5px;
    max-width: 800px;
    width: 100%; /* Ensure it takes full width of its parent */
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
    .text-container {
        padding: 1.5rem;
    }

    .text-container p {
        font-size: 0.9rem;
    }

    .title-container {
        text-align: center; /* Center the title text on mobile */
        width: 100%; /* Make it full-width on mobile for a better look */
    }

    .title-container h1 {
        font-size: 1.5rem;
    }
}