.text-container,
.photo-grid-container {
    width: 100%;
    max-width: 900px;
    margin: 2rem auto;
    padding: 0 1rem; /* Add some horizontal padding */
}

.text-container p {
    line-height: 1.6;
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
    .text-container,
    .photo-grid-container {
        margin: 1rem auto;
        padding: 0 1.5rem;
    }

    .text-container p {
        font-size: 0.9rem; /* Scaled down font size */
    }
}