/* Keyframe animation for the button effect */
@keyframes expand-and-fade {
    0% {
        max-width: 0;
        padding-left: 0;
        padding-right: 0;
        color: transparent;
    }
    70% {
        max-width: 500px;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        color: transparent;
    }
    100% {
        max-width: 500px;
        color: var(--text-color);
    }
}

.contact-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
}

.contact-links a {
    font-size: 1.5rem;
    margin: 1rem 0;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    transition: background-color 0.3s ease, border-color 0.3s ease, transform 0.3s ease;
    text-align: center;

    /* Set initial state to prevent flash upon page load */
    max-width: 0;
    padding: 0.5rem 0.5rem;
    color: transparent;
    overflow: hidden;

    animation: expand-and-fade 1s forwards;
}

/* Stagger the animation for each button */
.contact-links a:nth-child(1) {
    animation-delay: 0.2s;
}
.contact-links a:nth-child(2) {
    animation-delay: 0.4s;
}
.contact-links a:nth-child(3) {
    animation-delay: 0.6s;
}

.contact-links a:hover {
    background-color: var(--accent-color);
    border-color: var(--accent-border-color);
    transform: translateY(-2px) scale(101%);
}