.project-card {
    border: 1px solid var(--border-color);
    padding: 1.5rem;
    border-radius: 5px;
    margin-bottom: 2rem;
    width: 100%;
    max-width: 800px;
    transition: all 0.3s ease-out;
    min-height: 220px; /* This will ensure all cards have the same height */
    min-width: 50rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.project-card:hover {
    transform: translateY(-5px) scale(101%);
    border-color: var(--accent-border-color);
    background-color: var(--accent-color);
    border-radius: 15px;

}

.project-card h3 {
    margin-top: 0;
}

.project-links a {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    border: 1px solid var(--border-color);
    border-radius: 5px;
    transition: all 0.2s ease-out;
    margin-right: 1rem;
}

.project-links a:hover {
    transform: translateY(-2px) scale(101%);
    border-color: var(--accent-border-color);
    border-radius: 10px;
}

/* Responsive styles for smaller screens */
@media (max-width: 850px) {
    .project-card {
        min-width: 0;
        padding: 1rem;
    }

    .project-card h3 {
        font-size: 1.2rem;
    }

    .project-card p {
        font-size: 0.9rem;
    }

    .project-links a {
        font-size: 0.9rem;
    }
}